.slider {
  display: contents;
  width: 100vw;
  overflow: hidden;
  position: relative;
  //margin: 2.5rem auto;
}
.slides {
  display: flex;
  width: calc(100vw * 4);
  animation: glisse 30s 2;
  padding-top: 5vh;
}

.slide {
  display: flex;
  flex-direction: column;
  width: 100vw;

  .taille-slide {
    width: 90vw;
    height: 100%;
    margin: auto;
    z-index: 1000;
    filter: drop-shadow(10px 10px 6px black);
  }
  .slide__description {
    font-family: "poppins", sans-serif;
    padding: 2.5vh 0 5vh 0;
    text-shadow: 1.5vh 1vh 1.5vh rgb(0 0 0);
    margin: auto;
  }
}

.vignette__fleche__gauche {
  position: absolute;
  z-index: 10;
  opacity: 0.5;
  left: 2px;
  top: 40%;
  border: none;
  padding: 0;
  cursor: pointer;
  width: 20px;
}
.vignette__fleche__droite {
  position: absolute;
  z-index: 10;
  opacity: 0.5;
  right: 2px;
  top: 40%;
  border: none;
  padding: 0;
  cursor: pointer;
  width: 20px;
}
.fleche__droite,
.fleche__gauche {
  width: 3vh;
}
